html {
  scroll-behavior: smooth;
}
:root {
  --ledger-stone: #a1a69d;
  --gallery-plaster: #dadada;
  --archive-slate: #252e3a;
  --paper-white: #f5f5f5;
  --deep-patina: #294344;
}
body {
  font-family: var(--font-open-sans), -apple-system, Roboto, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
}
.nav-link {
  padding: 12px 8px;
  color: var(--archive-slate);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: none;
  transition: opacity 0.2s;
}
.nav-link {
  border: none;
  background: none;
  cursor: pointer;
}
.nav-link:hover {
  opacity: 0.7;
}
.offerings-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 10px;
  background: white;
  border: 1px solid var(--gallery-plaster);
  border-radius: 8px;
  min-width: 220px;
  box-shadow: 0 2px 8px rgba(37, 46, 58, 0.08);
  z-index: 50;
  overflow: hidden;
  animation: fadeIn 0.15s ease-out;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-2px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.offerings-dropdown-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 14px;
  color: var(--archive-slate);
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: none;
  transition: background-color 0.15s, color 0.15s;
  width: 100%;
  text-align: left;
  border-bottom: 1px solid var(--gallery-plaster);
  cursor: pointer;
}
.offerings-dropdown-item:last-child {
  border-bottom: none;
}
.offerings-dropdown-item:hover {
  background-color: rgba(161, 166, 157, 0.08);
  color: var(--archive-slate);
}
.offerings-dropdown-item:focus {
  outline: 2px solid var(--ledger-stone);
  outline-offset: -2px;
}
.offerings-dropdown-item-active {
  color: var(--archive-slate);
  background-color: rgba(161, 166, 157, 0.12);
}
.offerings-dropdown-indicator {
  display: inline-block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: var(--ledger-stone);
  margin-left: 8px;
  flex-shrink: 0;
}
.offerings-dropdown-wrapper {
  position: relative;
  padding-bottom: 150px;
  margin-bottom: -150px;
}
.nav-separator {
  color: var(--archive-slate);
  font-size: 16px;
  line-height: 24px;
  padding: 0 4px;
}
.btn-primary {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  padding: 8px 32px !important;
  background: var(--ledger-stone) !important;
  color: var(--paper-white) !important;
  font-size: 16px !important;
  font-weight: 400 !important;
  line-height: 24px !important;
  border: 2px solid var(--ledger-stone) !important;
  cursor: pointer !important;
  transition: opacity 0.2s !important;
  text-decoration: none !important;
}
.btn-primary:hover {
  opacity: 0.9 !important;
}
.hero-title {
  color: var(--archive-slate);
  font-family: Georgia, serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 1.2;
  margin-bottom: 16px;
}
.hero-subtitle {
  color: var(--archive-slate);
  font-family: Georgia, serif;
  font-size: 22px;
  font-weight: 300;
  line-height: 30px;
  margin-bottom: 16px;
}
.hero-description {
  color: var(--archive-slate);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.625;
  margin-bottom: 24px;
}
.cta-primary {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 32px;
  background: var(--ledger-stone);
  border: 2px solid var(--ledger-stone);
  color: var(--paper-white);
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  cursor: pointer;
  transition: opacity 0.2s;
  white-space: nowrap;
  text-decoration: none !important;
}
.cta-primary:hover {
  opacity: 0.9;
}
.cta-secondary {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 32px;
  background: transparent;
  border: 2px solid var(--ledger-stone);
  color: var(--archive-slate);
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}
.cta-secondary:hover {
  background: var(--ledger-stone);
  color: var(--paper-white);
}
@media (min-width: 640px) {
  .hero-title {
    font-size: 42px;
  }
  .hero-subtitle {
    font-size: 20px;
  }
  .hero-description {
    font-size: 15px;
  }
}
@media (min-width: 1024px) {
  .hero-title {
    font-size: 56px;
    line-height: 64px;
  }
  .hero-subtitle {
    font-size: 22px;
    line-height: 30px;
  }
  .hero-description {
    font-size: 16px;
    line-height: 26px;
  }
}
.section-heading {
  color: var(--archive-slate);
  font-family: Georgia, serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
  margin: 0;
  margin-bottom: 10px;
}
@media (min-width: 1024px) {
  .section-heading {
    font-size: 28px;
  }
}
section:has(.slider-wrapper) {
  overflow: visible !important;
}
.slider-wrapper {
  width: 100%;
  margin: 0;
  padding-left: 1rem;
  box-sizing: border-box;
  overflow: hidden;
}
@media (min-width: 640px) {
  .slider-wrapper {
    padding-left: 2rem;
  }
}
@media (min-width: 1024px) {
  .slider-wrapper {
    padding-left: 5rem;
  }
}
.slider-container {
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}
.artwork-slider {
  display: flex;
  gap: 18px;
  margin: 0;
  width: 100%;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  overflow-x: auto;
  overflow-y: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
  box-sizing: border-box;
}
.artwork-slider::-webkit-scrollbar {
  display: none;
}
.artwork-card {
  display: flex;
  flex-direction: column;
  gap: 4px;
  flex-shrink: 0;
  width: 210px;
}
@media (min-width: 1024px) {
  .artwork-card {
    width: 280px;
  }
}
.artwork-card img {
  pointer-events: none;
  user-select: none;
  -webkit-user-drag: none;
}
.artwork-image-wrapper {
  position: relative;
  width: 100%;
  aspect-ratio: 247 / 206;
  overflow: hidden;
  background: var(--gallery-plaster);
  contain: layout style paint;
}
.artwork-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.artwork-title {
  color: var(--archive-slate);
  font-family: Georgia, serif;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  margin: 0;
}
.artwork-details {
  color: var(--archive-slate);
  font-family: var(--font-open-sans), -apple-system, Roboto, Helvetica, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: normal;
  margin: 0;
}
.slider-nav-arrow {
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  background: transparent !important;
  border: none !important;
  border-radius: 0 !important;
  width: auto !important;
  height: auto !important;
  color: var(--archive-slate);
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0 !important;
  margin: 0 !important;
  flex-shrink: 0;
  box-shadow: none !important;
  outline: none !important;
}
.slider-nav-arrow:hover {
  color: var(--ledger-stone);
  transform: scale(1.1);
}
.slider-nav-arrow:active {
